@import "~normalize.css/normalize.css";
@import "~swiper/css/swiper.css";
@font-face {
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Radical";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Radical.woff2") format("woff2"), url("../fonts/Radical.woff") format("woff");
}
:root {
  font-size: 0.69445vw;
}
@media (max-width: 1024px) and (orientation: portrait) {
  :root {
    font-size: 3.125vw;
  }
}
@media (max-width: 1024px) and (orientation: portrait) and (min-width: 768px) {
  :root {
    font-size: 2.5vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  :root {
    font-size: 1vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) and (max-width: 768px) {
  :root {
    font-size: 2vw;
  }
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 1.8rem;
  line-height: 1.1;
  font-weight: 600;
  color: #ffffff;
  background-color: #5f458c;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 2000px) {
  body {
    font-size: 18px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body {
    min-height: calc(var(--vh, 1vh) * 100);
    font-size: 1.4rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body.menu-opened {
    overflow: hidden;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  body.menu-opened {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

.svg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -100;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes move-text {
  from {
    transform: translate3d(40px, 0, 0);
    opacity: 0;
  }
  to {
    transform: translate3d(0);
    opacity: 1;
  }
}
@keyframes move-text-reverse {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes move-intro-span {
  from {
    transform: translate3d(0, 13rem, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes change-color-form-btn {
  from {
    color: #ffffff;
  }
  to {
    color: #5f458c;
  }
}
@keyframes scale-width-togle {
  from {
    transform: scale(0.5);
  }
  80% {
    transform: scale(1.1);
  }
  to {
    transform: scale(1);
  }
}
@keyframes fill-screen {
  from {
    z-index: 6;
    height: 0;
  }
  100% {
    z-index: 6;
    height: 100vh;
  }
}
@keyframes appear-footer-opacity {
  0% {
    background-color: #ffffff;
    opacity: 0;
  }
  100% {
    background-color: #ffffff;
    opacity: 1;
  }
}
@keyframes show-button-rules {
  from {
    color: transparent;
    opacity: 0;
    font-size: 0;
  }
  80% {
    color: transparent;
    font-size: 2.4rem;
  }
  to {
    font-size: 2.4rem;
    opacity: 1;
    color: #5f458c;
  }
}
@media (min-width: 2000px) {
  @keyframes show-button-rules {
    from {
      color: transparent;
      opacity: 0;
      font-size: 0;
    }
    80% {
      color: transparent;
      font-size: 24px;
    }
    to {
      font-size: 24px;
      opacity: 1;
      color: #5f458c;
    }
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  @keyframes show-button-rules {
    from {
      color: transparent;
      opacity: 0;
      font-size: 0;
      width: 0;
    }
    80% {
      color: transparent;
      font-size: 1.8rem;
      width: 100%;
    }
    to {
      width: 100%;
      font-size: 1.8rem;
      opacity: 1;
      color: #5f458c;
    }
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  @keyframes show-button-rules {
    from {
      color: transparent;
      opacity: 0;
      font-size: 0;
    }
    80% {
      color: transparent;
      font-size: 1.8rem;
    }
    to {
      font-size: 1.8rem;
      opacity: 1;
      color: #5f458c;
    }
  }
}
@keyframes show-result-flamingo {
  from {
    opacity: 0;
    transform-origin: 80vw -20%;
    transform: rotate(-15deg);
  }
  to {
    opacity: 1;
    transform-origin: 80vw -20%;
    transform: rotate(0);
  }
}
@keyframes show-result-suitcase {
  from {
    transform-origin: 15vw 30vw;
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
@keyframes show-result-leaf-right {
  from {
    opacity: 0;
    transform-origin: -10vw bottom;
    transform: rotate(0deg);
  }
  15% {
    opacity: 0.2;
    transform-origin: -10vw bottom;
    transform: rotate(-10deg);
  }
  30% {
    opacity: 0.5;
    transform-origin: -10vw bottom;
    transform: rotate(10deg);
  }
  45% {
    opacity: 1;
    transform-origin: -10vw bottom;
    transform: rotate(-5deg);
  }
  60% {
    transform-origin: -10vw bottom;
    transform: rotate(3deg);
  }
  75% {
    transform-origin: -10vw bottom;
    transform: rotate(-2deg);
  }
  100% {
    transform-origin: -10vw bottom;
    transform: rotate(0deg);
  }
}
@keyframes show-result-leaf-left {
  from {
    opacity: 0;
    transform-origin: 30vw bottom;
    transform: rotate(5deg);
  }
  15% {
    opacity: 0.2;
    transform-origin: 30vw bottom;
    transform: rotate(10deg);
  }
  30% {
    opacity: 0.5;
    transform-origin: 30vw bottom;
    transform: rotate(-7deg);
  }
  45% {
    opacity: 1;
    transform-origin: 30vw bottom;
    transform: rotate(5deg);
  }
  60% {
    transform-origin: 30vw bottom;
    transform: rotate(-2deg);
  }
  75% {
    transform-origin: 30vw bottom;
    transform: rotate(1deg);
  }
  100% {
    transform-origin: 20vw bottom;
    transform: rotate(0deg);
  }
}
@keyframes show-result-leaf-green {
  from {
    opacity: 0;
    transform-origin: -10vw bottom;
    transform: rotate(-15deg);
  }
  15% {
    opacity: 0.2;
    transform-origin: -10vw bottom;
    transform: rotate(8deg);
  }
  35% {
    opacity: 0.5;
    transform-origin: -10vw bottom;
    transform: rotate(-5deg);
  }
  50% {
    opacity: 1;
    transform-origin: -10vw bottom;
    transform: rotate(3deg);
  }
  65% {
    transform-origin: -10vw bottom;
    transform: rotate(-2deg);
  }
  80% {
    transform-origin: -10vw bottom;
    transform: rotate(1deg);
  }
  100% {
    transform-origin: -10vw bottom;
    transform: rotate(0deg);
  }
}
@keyframes result__flamingo-move {
  0% {
    transform-origin: 20vw bottom;
    transform: rotate(0);
  }
  50% {
    transform-origin: 20vw bottom;
    transform: rotate(-4deg);
  }
  100% {
    transform-origin: 0;
    transform: rotate(0);
  }
}
@keyframes show-result-form-btn {
  0% {
    color: transparent;
    opacity: 0;
  }
  40% {
    color: transparent;
    opacity: 1;
  }
  100% {
    color: #5f458c;
  }
}
@keyframes move-btn-story-right {
  0% {
    opacity: 0;
    transform: translate3d(9rem, 0, 0) rotate(190deg);
  }
  100% {
    opacity: 1;
    transform: translate3d(0) rotate(0);
  }
}
@keyframes move-btn-story-left {
  0% {
    opacity: 0;
    transform: translate3d(-9rem, 0, 0) rotate(-190deg);
  }
  100% {
    opacity: 1;
    transform: translate3d(0) rotate(0);
  }
}
@keyframes appear-pagination {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes move-btn-result-to-left {
  0% {
    opacity: 0;
    transform: translate3d(8rem, 0, 0) rotate(80deg);
  }
  100% {
    opacity: 1;
    transform: translate3d(0) rotate(0);
  }
}
@keyframes appear-btn-result-text {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes move-lead {
  0% {
    transform: translate3d(0, 2rem, 0);
    opacity: 0;
  }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
@keyframes move-down-footer {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(0, 10rem, 0);
  }
}
@keyframes fill-story {
  0% {
    opacity: 1;
    z-index: 5;
  }
  100% {
    opacity: 0;
    z-index: 0;
  }
}
@keyframes show-prizes {
  0% {
    opacity: 1;
    z-index: 7;
    background: none;
  }
  100% {
    opacity: 1;
    z-index: 7;
    background: none;
  }
}
@keyframes hide-prizes-wrapper {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@media (prefers-reduced-motion: no-preference) {
  .game__text,
.prizes__text,
.rules__text,
.intro__text-date,
.intro__text,
.slider__text {
    overflow: hidden;
  }

  .page-header__menu {
    transition: all 0.4s cubic-bezier(0.36, 0.06, 0.12, 1.04) 200ms;
  }
  .page-header__menu a::after {
    transition: width 0.2s ease;
  }

  .page-header__logo {
    transition: all 100ms cubic-bezier(0.36, 0.06, 0.12, 1.04) 100ms;
  }

  .active-story {
    animation: fill-story 0s ease 0.4s both;
  }

  .screen-overlay--active {
    animation: fill-screen 0.4s ease;
  }

  .social-block__list li {
    transition: all 0.2s ease;
  }

  .social-block__list li:nth-child(1) {
    transition-delay: 100ms;
  }

  .social-block__list li:nth-child(2) {
    transition-delay: 200ms;
  }

  .social-block__list li:nth-child(3) {
    transition-delay: 300ms;
  }

  .intro__label {
    transition: all 500ms ease-in-out 1.6s;
  }

  .intro__message p {
    transition: all 300ms ease 1s;
  }

  .social-block__toggler {
    transition: opacity 100ms cubic-bezier(0.09, 0.82, 0.44, 0.89) 300ms;
  }
  .social-block__toggler::before {
    animation: scale-width-togle 200ms 1 forwards cubic-bezier(0.14, 0.22, 0.29, 1.06) 300ms;
  }

  .slider__item-text:nth-child(odd) {
    animation: move-text-reverse 0.1s 1 ease forwards;
  }

  .slider__item-text:nth-child(even) {
    animation: move-text-reverse 0.1s 1 ease forwards;
  }

  .swiper-slide-next .slider__item-text {
    animation: move-text 0.6s 1 forwards ease 350ms;
  }
  .swiper-slide-active .slider__item-text {
    animation: move-text 0.6s 1 forwards ease 200ms;
  }

  .screen--game.active .form__field {
    transition: all 400ms ease 100ms;
  }
  .screen--game.active .form__field::placeholder {
    transition: all 300ms ease 400ms;
  }

  .screen:not(.screen--game.screen--hidden) {
    transition: opacity 400ms ease, z-index 400ms ease;
  }

  .screen.screen--prizes {
    transition: z-index 400ms ease 400ms;
  }

  .screen.screen--rules {
    transition: z-index 400ms ease 400ms;
  }

  .screen--story:not(.screen--hidden) {
    transition: opacity 400ms ease, z-index 500ms ease;
  }

  .screen--show .form__field::placeholder {
    transition: all 300ms ease 1150ms;
  }

  .form__button {
    animation: change-color-form-btn 300ms 1 forwards linear 400ms;
  }

  .rules__item p {
    transition: all 0.4s ease 400ms;
  }
  .rules__item:nth-child(1) p {
    transition-delay: 180ms;
  }
  .rules__item:nth-child(2) p {
    transition-delay: 360ms;
  }
  .rules__item:nth-child(3) p {
    transition-delay: 540ms;
  }
  .rules__item:nth-child(4) p {
    transition-delay: 720ms;
  }
  .rules__item::before {
    transition: transform 200ms cubic-bezier(0.17, 0.67, 0.59, 1.51);
  }
  .rules__item:nth-child(1)::before {
    transition-delay: 140ms;
  }
  .rules__item:nth-child(2)::before {
    transition-delay: 280ms;
  }
  .rules__item:nth-child(3)::before {
    transition-delay: 420ms;
  }
  .rules__item:nth-child(4)::before {
    transition-delay: 560ms;
  }

  .rules__link {
    animation: show-button-rules 0.3s ease-in-out both 1.2s;
  }

  .active .rules__lead p, .active .prizes__lead p {
    animation: move-lead 300ms cubic-bezier(0.26, 0.77, 0.41, 0.94) both 400ms;
  }
  .active .slider__control--next {
    animation: move-btn-story-right 400ms 1 both cubic-bezier(0.17, 0.67, 0.35, 0.91) 600ms;
  }
  .active .slider__control--prev {
    animation: move-btn-story-left 400ms 1 both cubic-bezier(0.17, 0.67, 0.35, 0.91) 600ms;
  }
  .active .slider__pagination {
    animation: appear-pagination 200ms 1 both ease-out 600ms;
  }

  .screen .screen__wrapper {
    transition: all 200ms cubic-bezier(0.26, 0.77, 0.41, 0.94) 0.1s;
  }

  .screen--show .result__image-flamingo img {
    animation: show-result-flamingo 500ms cubic-bezier(0.28, 0.56, 0.05, 0.99) both 100ms, result__flamingo-move 2200ms ease-in infinite 500ms;
  }

  .screen--show .result__image-suitcase img {
    animation: show-result-suitcase 600ms cubic-bezier(0.49, 0.06, 0.26, 0.94) both;
  }

  .screen--show .result__image-leaf-right img {
    animation: show-result-leaf-right 1900ms cubic-bezier(0.4, 0, 0.2, 1) both 150ms;
  }

  .screen--show .result__image-leaf-left img {
    animation: show-result-leaf-left 2100ms cubic-bezier(0.4, 0, 0.2, 1) both 150ms;
  }

  .screen--show .result__image-leaf-green img {
    animation: show-result-leaf-green 1900ms cubic-bezier(0.4, 0, 0.2, 1) both 450ms;
  }

  .screen--show .form--result .form__field {
    transition: all 600ms ease 701ms, opacity 1ms ease 700ms;
  }

  .screen--show .result__button span {
    animation: move-btn-result-to-left 500ms 1 both cubic-bezier(0.21, 0.7, 0.4, 0.91) 300ms;
  }
  .screen--show .result__button {
    animation: appear-btn-result-text 600ms 1 both cubic-bezier(0.21, 0.7, 0.63, 0.57) 300ms;
  }

  .screen--show .form--result .form__button {
    animation: show-result-form-btn 300ms 1 both linear 600ms;
  }

  .screen--rules.screen--hidden .screen__disclaimer {
    animation: move-down-footer 200ms cubic-bezier(0.36, 0.06, 0.12, 1.04) both;
  }

  .active--footer {
    animation: show-prizes 1s ease;
  }
  .active--footer .screen__wrapper {
    animation: hide-prizes-wrapper 400ms ease;
  }
}
@media (prefers-reduced-motion: no-preference) and (min-width: 768px) and (orientation: landscape) {
  .screen--intro .screen__footer {
    transition: all 200ms cubic-bezier(0.36, 0.06, 0.12, 1.04);
  }
}
@media (prefers-reduced-motion: no-preference) {
  .screen--prizes.active .screen__footer {
    transition: transform 200ms cubic-bezier(0.36, 0.06, 0.12, 1.04);
  }
}
@media (prefers-reduced-motion: no-preference) {
  .screen--prizes.screen--hidden .screen__footer-note {
    transition: opacity 500ms ease;
  }
}
@media (prefers-reduced-motion: no-preference) {
  .screen--rules.screen--hidden .disclaimer {
    transition: transform 500ms ease;
  }
}
@media (prefers-reduced-motion: no-preference) {
  .screen--rules.active li {
    transition: all 500ms ease 200ms;
  }
}
@media (prefers-reduced-motion: no-preference) and (max-width: 1024px) and (orientation: portrait) {
  .screen__footer {
    transition: all 400ms ease 500ms;
  }
  .screen__footer--full {
    transition: all 500ms ease;
  }
  .screen__footer .screen__footer-wrapper {
    transition: all 200ms cubic-bezier(0.1, 0.65, 0.14, 0.99) 400ms;
  }
  .screen__footer > button {
    transition: all 200ms ease 100ms;
  }
  .screen__footer--full > button {
    transition: all 200ms ease;
  }
}
.animation-screen {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-image: url("../img/scene.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
  z-index: -1;
}
.animation-screen.active {
  z-index: 1;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene@2x.png");
  }
}
@media (orientation: portrait) {
  .animation-screen {
    background-image: url("../img/scene-mob.png");
  }
}
@media (orientation: portrait) and (min-resolution: 144dpi), (orientation: portrait) and (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene-mob@2x.png");
  }
}

.btn {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: relative;
  padding: 1.5rem 2.9rem;
  text-transform: uppercase;
  text-align: center;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3rem;
  color: #5f458c;
  letter-spacing: -0.04em;
}
.btn:focus {
  outline: none;
}
@media (min-width: 2000px) {
  .btn {
    padding: 15px 29px;
    font-size: 24px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
.btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 3.5rem;
  z-index: -1;
}
.btn:hover {
  color: #a67ee5;
}
.btn:focus {
  color: #a67ee5;
  outline: none;
}
.btn:focus::before {
  width: 103%;
  height: 108.5%;
  border-color: #a67ee5;
}
.btn:active, .btn:active:focus {
  color: #ffffff;
}
.btn:active::before, .btn:active:focus::before {
  width: 100%;
  height: 100%;
  background-color: #a67ee5;
  border-color: #a67ee5;
}

.chat {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.7rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .chat {
    padding: 0 57px;
  }
}
@media (orientation: portrait) {
  .chat {
    padding: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat {
    padding: 0;
  }
}

.chat__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.9rem 0;
  width: 100%;
  max-height: calc(100vh - 33.9rem);
  max-width: 79.8rem;
  overflow-y: auto;
}
@media (min-width: 2000px) {
  .chat__body {
    padding: 0 59px 0;
    max-height: calc(100vh - 339px);
    max-width: 798px;
  }
}
@media (orientation: portrait) {
  .chat__body {
    padding: 0 0 0;
    max-height: calc(var(--vh, 1vh) * 100 - 22.4rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .chat__body {
    max-height: calc(var(--vh, 1vh) * 100 - 25rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__body {
    padding: 0;
    max-height: calc(var(--vh, 1vh) * 100 - 16rem);
  }
}

.chat__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-top: auto;
}

.chat__message {
  flex: none;
  display: flex;
  margin-bottom: 2rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
}
@media (min-width: 2000px) {
  .chat__message {
    margin-bottom: 20px;
    font-size: 24px;
  }
